import React from "react";
import { Typography } from "antd";
import { isMobile } from "react-device-detect";
const { Title, Paragraph, Text, Link } = Typography;

function Robotics() {
  return (
    <div>
      <Title>RescueBot</Title>

      <iframe
        width={isMobile ? "100%" : "560"}
        height={isMobile ? "100%" : "315"}
        src="https://www.youtube.com/embed/BWogS4yuJig"
        title="YouTube video player"
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
        allowFullScreen
      ></iframe>

      <Paragraph>
        RescueBot is my team's final project submission for the Major Design
        Experience course EECS 467: Autonomous Robotics. RescueBot is envisioned
        as an assistive robot for first responder operations, including
        firefighting and search and rescue missions. The wheeled robot is
        equipped with a thermal infrared camera and an Intel Realsense camera
        for identifying thermal objects, which it plots in its SLAM generated
        location map. Our implementation serves as a proof of concept of
        RescueBot's ability to identify fires in an unexplored space, and locate
        and lead humans to safety.
      </Paragraph>

      <Paragraph>
        RescueBot is an extention of the Mbot-Mini platform which we worked on
        during the first part of EECS 467 and was completed over a period of 5
        weeks. The Mbot-Mini features a Raspberry Pi4 computing unit, a 2D
        RPLIDAR, and brushed DC wheeled motors controlled by a Raspberry Pi Pico
        microcontroller. Our team had previously implemented motor control,
        SLAM, and path planning algorithms.
      </Paragraph>
    </div>
  );
}

export default Robotics;
